@mixin nolist
  list-style: none
  margin: 0
  padding: 0

@mixin autocol($size)
  display: grid
  grid-template-columns: repeat(auto-fit, minmax($size, 1fr))

@mixin before
  &
    position: relative
  &:before
    content: ''
    display: block
    position: absolute
    z-index: -1
    @content
@mixin after
  &
    position: relative
  &:after
    content: ''
    display: block
    position: absolute
    z-index: -1
    @content

@mixin netflix($x, $y)
  &
    position: relative
    padding-top: calc(#{$x} / #{$y}*100%) // 56.25% // 16:9 Aspect Ratio
    & > *
      position: absolute
      left: 0
      top: 0
      width: 100%
      height: auto

@mixin m-tablet()
  @media (min-width: 700px)
    @content
