@use '../../sass/tokens.sass' as *
@use '../../sass/mixins.sass' as *

.hero
  display: grid
  width: 100%
  border-radius: 16px
  grid-template-rows: auto auto
  place-items: center

  &__title
    position: relative
    padding: 50px 50px 20px
    border-radius: 16px
    font-family: "Great Vibes", cursive
    /* font-family: "Caveat", cursive */
    font-size: 50px
    line-height: 1
    color: $c-main
    text-align: center
    margin-bottom: 20px
    @include m-tablet()
      max-width: calc(100% - 400px)
      padding: 20px 50px

    &::before,
    &::after
      position: absolute
      display: block
      background-position: center
      background-size: contain
      background-repeat: no-repeat
      content: ''
      mix-blend-mode: darken

  &--academy > &__title
    position: relative
    background: url(../imgs/pattern-1.png) 0 / 100px 150px

    &::before
      top: -110px
      left: 50%
      width: 200px
      height: 200px
      background-image: url(../imgs/bg-desktop-eye.png)
      transform: translateX(-50%)
      @include m-tablet()
        width: 200px
        height: 200px
        top: -50px
        left: -120px
        transform: initial

    &::after
      @include m-tablet()
        top: 50%
        right: -160px
        width: 250px
        height: 300px
        background-image: url(../imgs/academy-bg-nose.png)
        transform: translateY(-50%)

  &--zhivopis > &__title
    background: url(../imgs/pattern-5.png) 0 / 100px 150px

    &::before
      bottom: -50px
      left: -100px
      width: 250px
      height: 250px
      background-image: url(../imgs/palette.avif)

    &::after
      top: 60%
      right: -130px
      width: 250px
      height: 300px
      background-image: url(../imgs/brushes.webp)
      transform: translateY(-50%)

  &--leather > &__title
    color: #fff
    background: url(../imgs/pattern-leather-6.jpg) 0 / cover


    &::before
      bottom: -90px
      left: -100px
      width: 160px
      height: 250px
      background-image: url(../imgs/leather-things-2.png)
      mix-blend-mode: normal

    &::after
      top: auto
      right: -70px
      bottom: -70px
      width: 140px
      height: 85px
      background-image: url(../imgs/leather-things-1.png)
      transform: translateY(-50%)
      mix-blend-mode: normal

  & > .logo
      display: block
      margin: 0 auto
      margin-top: 50px
      margin-bottom: 80px
      max-width: 200px

      @include m-tablet()
        margin-top: 0
        margin-bottom: 40px

